import React from "react";

function ListSongs({ setSongs, songs, music, setCurrentSong,currentSong}) {
  
const songClick = () =>{
    setCurrentSong(music);
}



    
 return (
    <div
      onClick={songClick}
      className={`list-song ${music.id ===currentSong.id ? "selected" : ""}`}
    >
      <img alt={music.name} src={music.cover}></img>
      <div className="description">
        <h3>{music.name}</h3>
        <h4>{music.artist}</h4>
      </div>
    </div>
  );
}

export default ListSongs;
